:root {
    --bs-border-color: var(--tc-system-stroke) !important;
    --bs-primary-rgb: 212, 44, 108;
    --bs-dark-rgb: 41, 41, 41;

    .border-header {
        --bs-border-color: #f94b8d;
    }

    .text-secondary-2 {
        color: #f8f2f3;
    }

    .bg-secondary-2 {
        background-color: #f8f2f3;
    }
}

.bg-light {
    background-color: var(--tc-bg-light) !important;
}

.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

.fill-white {
    path {
        fill: var(--tc-system-white) !important;
    }

    fill: var(--tc-system-white) !important;
}

.mask-image {
    mask-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1) 20%, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0));
}

.mask-image-2 {
    mask-image: linear-gradient(-270deg, #fff 14.77%, rgba(255, 255, 255, 0) 100%);
}

.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
