}

p {
    font-family: var(--tc-body-font-family);
    font-size: var(--tc-body-font-size);
    line-height: var(--tc-body-line-height);
    font-weight: var(--tc-fw-regular);
    color: var(--tc-body-text);
    margin-bottom: 15px;
}
.p {
    font-family: var(--tc-body-font-family);
    font-size: var(--tc-body-font-size);
    line-height: var(--tc-body-line-height);
    font-weight: var(--tc-fw-regular);
    color: var(--tc-body-text);
    margin-bottom: 15px;
}
hr {
    border-bottom: 1px solid var(--tc-color-gray-1);
    border-top: 0 none;
    margin: 30px 0;
    padding: 0;
}
label {
    color: var(--tc-heading-color);
    cursor: pointer;
    font-size: var(--tc-body-font-size);
    font-weight: var(--tc-fw-regular);
}
input[type='color'] {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    background: none;
    border: 0;
    cursor: pointer;
    height: 100%;
    width: 100%;
    padding: 0;
    border-radius: 50%;
}
/*=============================
    - Input Placeholder
===============================*/
input,
textarea {
    color: var(--tc-body-text);
}
*::-moz-placeholder {
    color: var(--tc-body-text);
    font-size: var(--tc-fs-7);
    opacity: 1;
}
*::placeholder {
    color: var(--tc-body-text);
    font-size: var(--tc-fs-7);
    opacity: 1;
}
.list-wrap {
    margin: 0px;
    padding: 0px;
    & li {
        list-style: none;
    }
}
img,
.img {
    max-width: 100%;
    transition: 0.3s;
}
a:focus,
.btn:focus,
.button:focus {
    text-decoration: none;
    outline: none;
    box-shadow: none;
}
a:hover {
    color: var(--tc-theme-primary) !important;
    text-decoration: none;
}
.btn:hover {
    color: var(--bs-btn-hover-color) !important;
    background: var(--bs-btn-hover-bg);
    border-color: var(--bs-btn-hover-border-color);
}
