textarea {
    &.message {
        background-image: none;

        &:focus {
            background-image: none;
            background-position: left 18px top;
            background-position-y: top 18px;
            background-repeat: no-repeat;
        }

        &:focus {
            box-shadow: none;
            border: 1px solid var(--tc-theme-primary);
        }
    }
}

.rotate-x-180 {
    transform: rotateX(180deg);
}

.custom-cursor-cover {
    position: fixed;
    z-index: 9999999;
    pointer-events: none;

    span.dot {
        position: fixed;
        width: 0.5em;
        height: 0.5em;
        border-radius: 50%;
        background-color: #707070;
        pointer-events: none;
        mix-blend-mode: difference;
    }
}

.follower {
    pointer-events: none;
    position: fixed;
    transition: all 0.09s ease;
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    background-color: transparent;
    mix-blend-mode: difference;
    border: 1px solid #707070;
    z-index: 9999999;
}

.typewrite > .wrap {
    /* Blinking cursor */
    padding-right: 5px;
}

/* Cursor blinking animation */
@keyframes blink {
    50% {
        opacity: 0;
    }
}

.typewrite > .wrap::after {
    content: '|';
    /* Cursor */
    animation: blink 0.7s infinite;
}

.parallax-item-wrap {
    perspective: 1000px;
}
