  --bs-primary-rgb: 212, 44, 108;
  --bs-dark-rgb: 41, 41, 41;
}
:root .border-header {
  --bs-border-color: #f94b8d;
}
:root .text-secondary-2 {
  color: #f8f2f3;
}
:root .bg-secondary-2 {
  background-color: #f8f2f3;
}

.bg-light {
  background-color: var(--tc-bg-light) !important;
}

.btn-text {
  font-family: var(--tc-btn-font-family);
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%;
  /* 14px */
  text-transform: uppercase;
}

.fill-primary {
  fill: var(--tc-theme-primary) !important;
}
.fill-primary path {
  fill: var(--tc-theme-primary) !important;
}

.fill-white {
  fill: var(--tc-system-white) !important;
}
.fill-white path {
  fill: var(--tc-system-white) !important;
}

.mask-image {
  mask-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgb(0, 0, 0) 20%, rgb(0, 0, 0) 80%, rgba(0, 0, 0, 0));
}

.mask-image-2 {
  mask-image: linear-gradient(-270deg, #fff 14.77%, rgba(255, 255, 255, 0) 100%);
}

.contact-map {
  max-height: 700px;
}
.contact-map iframe {
  height: 700px;
  width: 100%;
}

.map {
  -webkit-filter: grayscale(75%);
  -moz-filter: grayscale(75%);
  -ms-filter: grayscale(75%);
  -o-filter: grayscale(75%);
  filter: grayscale(75%);
}

.pagination_item {
  background: var(--tc-bg-light);
  color: var(--tc-system-black);
  border-radius: 8px;
  font-size: 20px;
  font-weight: 600;
}
.pagination_item svg path {
  stroke: var(--tc-system-black) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item.active {
  background: var(--tc-theme-primary) !important;
  color: var(--tc-bg-light) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item.active svg path {
  stroke: var(--tc-bg-light) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item:hover {
  background: var(--tc-theme-primary) !important;
  color: var(--tc-bg-light) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item:hover svg path {
  stroke: var(--tc-bg-light) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item:focus {
  box-shadow: none;
  background-color: var(--tc-theme-primary) !important;
  color: var(--tc-bg-light) !important;
  transition: 0.3s ease-in-out;
}
.pagination_item:focus svg path {
  stroke: var(--tc-bg-light) !important;
  transition: 0.3s ease-in-out;
}

/* Popup Content */
.popup-content {
  background: #fff;
}
.popup-content .popup-icon img {
  height: 100px;
  width: 100px;
  object-fit: contain;
}

