.swiper-button-next,
.swiper-button-prev {
  --swiper-navigation-size: 16px;
  --swiper-navigation-top-offset: 50%;
  --swiper-navigation-sides-offset: 10px;
  --swiper-navigation-color: var(--tc-system-black);
  display: flex;
  width: 40px;
  height: 40px;
  padding: 0px;
  font-size: 16px;
  border-radius: 30px;
  border: 1px solid var(--tc-system-stroke);
}
.swiper-button-next svg,
.swiper-button-prev svg {
  width: 16px;
  height: 16px;
}
.swiper-button-next:hover svg path,
.swiper-button-next :active svg path,
.swiper-button-prev:hover svg path,
.swiper-button-prev :active svg path {
  fill: var(--tc-theme-primary);
}

.swiper-button-next::after,
.swiper-button-prev::after {
  content: "";
}

.swiper-pagination-bullet {
  width: 6px;
  height: 6px;
  background: #5a5a5a;
}

.swiper-pagination-bullet-active {
  width: 6px;
  height: 6px;
  background: var(--tc-system-black);
}

.hover-up {
  transition: all 0.3s ease-in-out;
}
.hover-up:hover {
  transform: translateY(-3px);
  transition-duration: 0.3s;
}

.hover-effect {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: all 0.5s;
  border: none;
  text-align: center;
}

.hover-effect:after {
  font: var(--fa-font-solid);
  content: "\f105";
  color: var(--tc-theme-primary);
  position: absolute;
  opacity: 0;
  left: -10px;
  transition: 0.5s;
  top: 5px;
}

.hover-effect:hover {
  opacity: 1 !important;
  padding-left: 20px;
  padding-right: 8px;
}

.hover-effect:hover:after {
  opacity: 1;
  left: 0px;
}

.hover-effect-1 {
  transition: 0.2s;
}
.hover-effect-1:hover {
  padding-left: 4px;
}

.circle-text {
  position: absolute;
  margin-bottom: 0;
  font-size: 15px;
  text-transform: uppercase;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.circle-text span {
  position: absolute;
  top: -74px;
  display: inline-block;
  transform-origin: 0 74px;
}

