.play-btn {
    font-size: 24px;
    line-height: 32px;
    position: relative;
    background-color: rgba(255, 255, 255, 1);
    &:before {
        content: '';
        position: absolute;
        width: 150%;
        height: 150%;
        -webkit-animation-delay: 0s;
        animation-delay: 0s;
        -webkit-animation: pulsate1 2s;
        animation: pulsate1 2s;
        -webkit-animation-direction: forwards;
        animation-direction: forwards;
        -webkit-animation-iteration-count: infinite;
        animation-iteration-count: infinite;
        -webkit-animation-timing-function: steps;
        animation-timing-function: steps;
        opacity: 1;
        border-radius: 50%;
        border: 5px solid var(--tc-border-1);
        box-shadow: rgba(255, 255, 255, 0.78) 0px 2px 4px 0px;
    }
}
@-webkit-keyframes pulsate1 {
    0% {
        -webkit-transform: scale(0.8);
        transform: scale(0.8);
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: 0;
        box-shadow: none;
    }
}
@keyframes pulsate1 {
    0% {
        -webkit-transform: scale(0.8);
        transform: scale(0.8);
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(1, 1);
        transform: scale(1);
        opacity: 0;
        box-shadow: none;
    }
}
.button--calypso {
    overflow: hidden;
    position: relative;
    svg {
        z-index: 2;
    }
}

.button--calypso span {
    display: block;
    position: relative;
    z-index: 10;
}

.button--calypso:hover span {
    animation:
        MoveScaleUpInitial 0.3s forwards,
        MoveScaleUpEnd 0.3s forwards 0.3s;
}
.button--calypso:hover {
    background: transparent !important;
}
@keyframes MoveScaleUpInitial {
    to {
        transform: translate3d(0, -105%, 0) scale3d(1, 2, 1);
        opacity: 0;
    }
}

@keyframes MoveScaleUpEnd {
    from {
        transform: translate3d(0, 100%, 0) scale3d(1, 2, 1);
        opacity: 0;
    }
    to {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}
// btn-calypso
.button--calypso::before {
    content: '';
    background: var(--tc-theme-primary);
    width: 120%;
    height: 0;
    position: absolute;
    padding-bottom: 120%;
    top: -110%;
    left: -10%;
    border-radius: 50%;
    transform: translate3d(0, 68%, 0) scale3d(0, 0, 0);
}

.button--calypso:hover::before {
    transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
    transition: transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
}

.button--calypso::after {
    content: '';
    background: var(--tc-theme-primary);
    position: absolute;
    transform: translate3d(0, -100%, 0);
    transition: transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.button--calypso:hover::after {
    transform: translate3d(0, 0, 0);
    transition-duration: 0.05s;
    transition-delay: 0.4s;
    transition-timing-function: linear;
}
// btn-change-text
.btn-change-text {
    --bs-btn-border-radius: 10px;
    position: relative;
    display: inline-flex;
    align-items: center;
    overflow: hidden;
    padding: 19px 30px;
    border: 1px solid var(--bs-btn-border-color);
    background: var(--bs-btn-bg);
    color: var(--bs-btn-color);
    border-radius: var(--bs-btn-border-radius);
    font-size: 14px;
    gap: 10px;
    font-weight: 600;
    text-transform: uppercase;
    span {
        transition: all 0.35s ease;
    }
    svg {
        path {
            transition: 0.15s ease-in-out;
        }
    }
    &:before {
        content: attr(data-text);
        color: var(--bs-btn-hover-color);
        position: absolute;
        transition: all 0.35s ease;
        top: -35px;
    }
    &:hover {
        background: var(--bs-btn-hover-bg);
        border-color: var(--bs-btn-hover-border-color);
        span {
            transform: translateY(300%);
            transition: all 0.35s ease;
        }
        svg {
            path {
                fill: var(--bs-btn-hover-color) !important;
            }
        }
        &:before {
            top: 50%;
            transform: translateY(-50%);
            transition: all 0.35s ease;
        }
    }
}
/* Fill down */
.btn-down {
    border: none;
    outline: none;
    background: #fff;
    text-align: center;
    display: inline-block;
    position: relative;
    overflow: hidden;
    transition: color 0.5s;

    &:before {
        content: '';
        position: absolute;
        z-index: -1;
        height: 200px;
        width: 250px;
        border-radius: 50%;
        bottom: 100%;
        left: -30px;
        transition: all 0.7s;
        background-color: var(--tc-theme-primary) !important;
    }

    &:hover {
        color: #fff;

        &:before {
            bottom: -50px;
        }
    }
}

/* Slide from bottom */
.btn-slide-bottom {
    border: none;
    border-radius: 0 !important;
    text-align: center;
    cursor: pointer;
    overflow: hidden;
    transition: all 0.5s ease-in-out;
    position: relative;

    span {
        position: relative;
        z-index: 1;
    }
    svg {
        position: relative;
        z-index: 1;
    }

    &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        height: 490%;
        width: 140%;
        transition: all 0.5s ease-in-out;
        transform: translateX(-100%) translateY(0%) rotate(35deg);
    }

    &:hover {
        span {
            color: #fff;
        }
        svg path {
            fill: #fff !important;
        }
        &::after {
            transform: translateX(-9%) translateY(-25%) rotate(90deg);
        }
    }

    &--red {
        &:after {
            background: var(--tc-theme-primary) !important;
        }
    }
    &--red-2 {
        &:after {
            background: #97071f !important;
        }
    }
}
